*{
  box-sizing: border-box;
}
.App {
  text-align: center;
  background-color: rgb(255, 255, 255);
}

body{
  background-color: rgb(255, 255, 255);
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.container{
  max-width: 1440px;
  margin: 0 auto;
}

.nav-wrapper{
  display: flex;
  justify-content: space-between;
  margin: 0 50px 0 50px;
  align-items: center;
  align-content: center;
}

.links{
  display: flex;
  list-style: none;
  color: black;
  margin-right: 30px;
}

.nav-item{
  text-decoration: none;
  margin-right: 40px;
  font-size: 30px;
  color: rgb(0, 0, 0);
  font-weight: 900;
}
.nav-item:hover{
  transition: 1s;
  color: rgb(138, 138, 138);

}

.logo{
  width: 90px;
}


.window{
  margin-top: 50px;
}


.chat-form__input{
  display: inline-block;
  border-radius: 5px;
  padding: 15px 35px;
  font-size: 22px;
  font-weight: 600;
  text-decoration: none;
  margin: 20px;
  color: rgb(0, 0, 0);
  background: rgb(255, 255, 255);
  border:none;
  transition: .3s;
  box-shadow: 10px 10px 10px rgba(0,0,0,0.5);
  outline:none;
}

.chat-form__btn{
  display: inline-block;
  border-radius: 5px;
  padding: 15px 35px;
  font-size: 22px;
  font-weight: 600;
  text-decoration: none;
  margin: 20px;
  color: rgb(255, 255, 255);
  background: rgb(0, 0, 0);
 cursor: pointer;
  box-shadow: 10px 10px 10px rgba(0,0,0,0.5);
  transition: .3s;
}

.chat-form__input::placeholder {color: #000000;}

.app_title{
 margin-left: 300px;
}

.elements{
  display: flex;
  justify-content:space-between;
  align-items: center;
  align-content: center;
}
.name_wrapper{
  display: flex;
  justify-content:space-between;
  align-items: center;
  align-content: center;
}
.message_wrapper{
  border: none;
  position: relative;
  border-radius: 10px;
  max-width: 700px;
}

.delete_button{
    height: 20px;
    width: 20px;
    position: absolute;
    top:-10px;
    right: -10px;
    background-color: white;
    color: rgb(224, 118, 118);
    background-color: #ffffff;
    border-radius: 1000px;
    border:none;
    font-size: 10px;
    font-weight: 900;
    cursor: pointer;
    transition: 1s;
}

.delete_button:hover{
  transform: rotate(360deg);
  transition: 1s;
}

.message_content{
  max-width:400px;
  word-wrap: break-word;
  display: inline-block;
  font-size: 30px;
}

.chat_form{
  margin-top: 20px;
}

.bot_style{
  margin-left: 10px;
  background-color: antiquewhite;
  animation: anim_car8 linear 3s;

  
}

.person_style{
  margin: 10px;
  margin-left: 200px;
  background-color: #61dafb;
}

.chat-messages__content{
  height: 450px;
  width: 900px;
  overflow-y: auto;
  overflow-x: hidden;
  border: none;
  padding-top: 20px;
  padding-bottom: 20px;
}

.chat-messages__content::-webkit-scrollbar {
  width: 1px;
  
}

.chat-messages__content{
  margin: 0 auto;
}


.app_title{
  font-family:'Courier New', Courier, monospace;
  font-size: 50px;
}

.person_name{
  display:inline-block;
  font-size: 40px;
  font-weight: 600;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  margin-left:7px ;
}

.date_message{
  margin-right: 5px;
  
}


.remove-btn {
  margin-right: 0.5rem;
}

.item-enter {
  opacity: 0;
}
.item-enter-active {
  opacity: 1;
  transition: opacity 500ms ease-in;
}
.item-exit {
  opacity: 1;
}
.item-exit-active {
  opacity: 0;
  transition: opacity 500ms ease-in;
}


